p {
  font-size: 16px;
}

h5 {
  position: relative;
  z-index: 1;
  overflow: hidden;
  text-align: center;
}

h5:before,
h5:after {
  position: absolute;
  top: 50%;
  overflow: hidden;
  height: 1px;
  width: 50%;
  content: "\a0";
  background-color: black;
}

h5:after {
  margin-left: 5%;
}

h5:before {
  margin-left: -55%;
  text-align: right;
}

.App {
  text-align: center;
  background-color: #fff6d9;
}

.App-header {
  min-height: 20vh;
  font-size: calc(50px + 2vmin);
  color: #ffb535;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.component {
  min-height: 80vh;
}

.workout-form {
  background-color: #fcdc80;
  border-radius: 15px;
  text-align: left;
  padding: 1em;
  font-size: 16px;
  font-weight: 500;
}

.form-labels {
  font-weight: bold;
  color: black;
  text-shadow: -1px -1px 0 #fff6d9, 1px -1px 0 #fff6d9, -1px 1px 0 #fff6d9, 1px 1px 0 #fff6d9;
}

.labels {
  width: 10em;
}

.labels:hover {
  cursor: pointer;
}

.form-group {
  height: 5em;
}

.error {
  background-color: #fff6d9 !important;
  color: #ffb535;
  font-weight: bold;
  border: 1px solid #fff6d9 !important;
  border-radius: 10px;
  text-align: center;
}

button#form-button,
button#form-button:focus,
button#back-button:hover,
button#back-button:focus,
button#back-button:active {
  background-color: #ffb535 !important;
  border: 1px solid #fff6d9 !important;
  color: #fff6d9 !important;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  outline: none;
}

button#form-button:hover,
button#form-button:active,
button#back-button {
  cursor: pointer;
  background-color: #fff6d9 !important;
  color: #ffb535 !important;
  border: 1px solid #ffb535 !important;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  outline: none;
}

.length-select,
.opt:hover {
  cursor: pointer;
}

.workout-result {
  background-color: #fcdc80;
  border-radius: 15px;
  text-align: left;
  padding: 2em;
  font-size: 16px;
  font-weight: 500;
  margin-top: 2em;
}

.set {
  margin-left: 1em;
}

.list-item {
  font-size: 16px;
}

#section-description {
  font-size: 16px;
  text-align: center;
  text-align: justify;
  padding-left: 2em;
  padding-right: 2em;
}

#bootstrap-overrides input[type="radio"],
#bootstrap-overrides input[type="checkbox"] {
  margin: 0px 5px 0px;
}
